*{
    margin: 0;
    padding: 0;
    font-family: Metropolis;
    overflow: hidden;
}
@font-face {
    font-family: Metropolis;
    font-weight: bold;
    src: url('./assets/fonts/Metropolis-Bold.otf');
}
@font-face {
    font-family: Metropolis;
    src: url('./assets/fonts/Metropolis-Regular.otf');
}
.chat-page{
    display: flex;
}
.button{
    width: 1rem;
}
.icon{
    border: none;
    outline: none;
    background: none;
    background-repeat: no-repeat;
    background-size: 100%;
    padding: .1rem;
    margin: 0.25rem;
    width: 1.5rem;
    height: 1.5rem;
    transition: all 0.5s;
}
.icon:hover{
    cursor: pointer;
    transform: scale(1.1);
    transition: transform 0.2s;
}