.messages{
    width: 20%;
    height: 100vh;
    overflow-y: scroll;
    border-right: 1px solid whitesmoke;
    position: relative;
}
.header{
    width: 100%;
    padding: 0.5rem;
    padding-top: 5%;
    display: flex;
    align-items: center;
    border-bottom: 1px solid whitesmoke;
}
.title{
    font-size: 1.5rem;
}
.header *{
    padding: 0.5rem;
}
.profile-photo{
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
}