:root{
    --main-blue: #0099FF;
}
.friend{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 2px solid whitesmoke;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-bottom: 0.75rem;
}
.friend:hover{
    cursor: pointer;
    background-color: whitesmoke;
    transition: all 0.2s;
}
.friend * {
    padding: 0.5rem;
}
.friend-info{
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: flex-start;
    margin-top: 0.25rem;
}
.friend-info *{
    padding: 0.1rem;
}
.friend-name{
    font-size: 1.25rem;
}
.friend-time{
    font-size: 0.75rem;
    color: var(--main-blue);
    font-weight: bold;
}
.notification{
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    padding: 0;
    padding-right: 0.5rem;
}
.notification-icon{
    width: 1.5rem;
    float: right;
    padding: 0;
}
.messages-number{
    margin: 0;
    padding: 0;
    font-size: 0.75rem;
    color: var(--main-blue);
    font-weight: bold;
}
.photo{
    border-radius: 50%;
    width: 2.5rem;
    height: 2.5rem;
}
