.chat{
    width: 65%;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
.info-bar{
    display: flex;
    align-items: center;
    padding: 0.25rem;
    border-bottom: 1px solid whitesmoke;
}
.info-bar *{
    padding: 0.5rem;
}
.info-bar-name{
    font-size: 1.5rem;
}
/*chat messages*/
.chat-messages-container{
    height: 100%;


    overflow-y: scroll;

}
.chat-messages{
    height: auto;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
/*chat bar*/
.chat-bar{
    width: 100%;
    padding: 0.25rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-top: 1px solid whitesmoke;
}
.chat-bar *{
    padding: 0.5rem;
}
.text-area{
    width: 80%;
    border-radius: 4000px;
    border: none;
    outline: none;
    background-color: whitesmoke;
    padding: 0.5rem;
    padding-left: 1rem;
    font-size: 1rem;
}