:root{
    --main-blue: #0099FF;
}
.message-container{
    max-width: 40%;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding-left: 0.25rem;
    padding-right: 0.25rem;
    margin: 0.5rem;
}
.message{
    padding: 0.75rem;
    margin-bottom: 0.5rem;
    max-width: 100%;
    border-radius: 0.75rem 0.75rem 0.1rem 0.75rem;
    box-shadow: 0 3px 10px #00000030;
    text-align: justify;
    color: white;
    background-color: var(--main-blue);
    font-size: 1rem;
    animation: message-fade-in;
    animation-duration: 0.2s;
}
.message-time-stamp{
    font-size: 0.7rem;
    color:#d8d8d8;
}

@keyframes message-fade-in {
    0% {
        transform: translateY(1rem);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}