:root{
    --main-blue: #0099FF;
}
.navigation{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    background-color: var(--main-blue);
    padding: 0.5rem;
    position: absolute;
    bottom: 0;
}
.nav-icon{
    width: 1.5rem;
    transition: all 0.5s;
}
.nav-icon:hover{
    cursor: pointer;
    transform: scale(1.1);
    transition: transform 0.2s;
}